header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}
.header_container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* ===========CTA=================== */
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}
/* ===========Social header=================== */
.head__social {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}
.head__social::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}
/* ===========me=================== */
.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  right: 0;
  top: 3%;
  transform: translate(-100%, 35%);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 0rem 1.5rem 1.5rem 1.5rem;
}

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  font-size: 0.9rem;
  font-weight: 300;
  transform: rotate(90deg);
}
/* ================Media Quary (Medium Device)============ */
@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }

  .me {
    transform: translate(-35%, 35%);
  }
}
/* ================Media Quary (Small Device)============ */
@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }

  .head__social,
  .scroll__down {
    display: none;
  }
  .me {
    transform: translate(6%, 35%);
  }
}
